.certifications-section {
  padding: 80px 0;
  background-color: #0f172a; /* Dark background for neon effect */
  color: #fff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.section-title {
  font-size: 36px;
  font-weight: 700;
  text-align: center;
  color: #fff;
  margin-bottom: 60px;
  padding: 30px 0 40px;
  position: relative;
  margin-top: 30px;
  text-shadow: 0 0 10px #0ff, 0 0 20px #0ff; /* Cyan neon glow */
}

/* You could also add a decorative underline */
.section-title:after {
  content: '';
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: #0ff; /* Cyan neon */
  box-shadow: 0 0 10px #0ff, 0 0 20px #0ff; /* Neon glow effect */
}

.certifications-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 32px;
}

.certification-card {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 255, 255, 0.2);
  overflow: hidden;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
}

.certification-card:hover {
  box-shadow: 0 10px 20px rgba(0, 255, 255, 0.3);
  transform: translateY(-5px);
}

.certification-image-container {
  height: 192px;
  overflow: hidden;
}

.certification-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.certification-content {
  padding: 24px;
}

.certification-title {
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 8px;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.certification-meta {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.certification-issuer {
  color: #0ff; /* Cyan */
  font-weight: 500;
}

.meta-separator {
  margin: 0 8px;
  color: #d1d5db;
}

.certification-date {
  color: #d1d5db;
}

.certification-description {
  color: #d1d5db;
  margin-bottom: 16px;
  line-height: 1.6;
}

.view-certificate-button {
  margin-top: 16px;
  background-color: transparent;
  color: #0ff; /* Cyan */
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 500;
  border: 1px solid #0ff;
  cursor: pointer;
  transition: all 0.3s ease;
  text-shadow: 0 0 5px #0ff;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.3);
}

.view-certificate-button:hover {
  background-color: rgba(0, 255, 255, 0.1);
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.5);
}

/* Media query for medium screens */
@media (min-width: 768px) {
  .certifications-grid {
    grid-template-columns: 1fr 1fr;
  }
}

/* Media query for large screens */
@media (min-width: 1024px) {
  .certifications-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}